@media (max-width: 992px) {
    .mainPic {
        max-width: 300px;
        max-height: 250px;
      }
}

.mainPic {
    width: 350px;
    height: 300px;
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;

    background-image: url("../image/Photo.jpg");
    background-size: cover;
    border-top-left-radius: 100% 30px;
    border-bottom-right-radius: 100% 30px;
}

