@import "./src/common/variables.scss";
@import "./src/common/mixin";

@media (max-width: 992px) {
    .mainText {
        min-width: 90%;
        padding-top: 100px;
    }
    .mainTextHello {
        margin-bottom: 15px;
    }
    .mainTextName {
        font-size: 35px;
        line-height: 1;
    }
}

.mainText {
    max-width: 60%;
    @include flex(flex, column, flex-start, start);
    margin: 0 auto;

    .mainTextHello {
        color: $bgColorButtonAndHighlightText;
        font-size: 24px;
        font-weight: 700;
        line-height: 1.3;

    }
    .mainTextName {
        margin-top: -10px;
    }
    span {
        div {
            font-weight: 600;
            font-size: 24px;
            line-height: 31px;
            color: $textColorMain;
            margin: 20px 0 15px;
        }
    }
    .mainTextTyping {
        width: 100%;
        height: 20px;
    }
}



